/*公共样式--开始*/
html, body, div, ul, li, h1, h2, h3, h4, h5, h6, p, dl, dt, dd, ol, form, input, textarea, th, td, select {
    margin: 0;
    padding: 0;
}

*{box-sizing: border-box;}

html, body {
    min-height: 100%;
}

body {
    font-family: "Microsoft YaHei";
    font-size:14px;
    color:#333;
}
h1, h2, h3, h4, h5, h6{font-size: 100%;}
ul,ol {
    list-style: none;
}

img {
    border: none;
    vertical-align: middle;
}

a {
    text-decoration: none;
    color: #232323;
}

table {
    border-collapse: collapse;
    table-layout: fixed;
}

input, textarea {
    outline: none;
    border: none;
}

textarea {
    resize: none;
    overflow: auto;
}

.clearfix {
    zoom: 1;
}

.clearfix:after {
    clear: both;
    content: ' ';
    display: block;
    height: 0;
    visibility: hidden;
}

.fl {
    float: left
}

.fr {
    float: right
}

.tl {
    text-align: left;
}

.tc {
    text-align: center
}

.tr {
    text-align: right;
}

.ellipse {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.inline{
    display: inline-block;
    *display: inline;
    *zoom: 1;
}
.headnav .logo, .head-login, .nav-hot{
    background: url(data:image/png;base64,) no-repeat 0 9999px;
}
.icn, .arr{
    background: url(../../static/media/toplist.721593c0.png) no-repeat 0 9999px;

}
.download, .download-btn{
    background: url(../../static/media/download.48c00478.png) no-repeat 0 0;
}
.side-login, .side-login a, .content-title, .more-tt, .new-items > li, .type-icon-play, .type-icon-good{
    background: url(../../static/media/index.5556d9cf.png) no-repeat 0 9999px;
    
}
.icon-listen, .btn, .hot-icon-play, .new-icon-play, .cur, .vol-alpha{
    background: url(../../static/media/iconall.de77152c.png) no-repeat 0 9999px;

}
.list-item-info, .item-dt-bc{
    background: url(../../static/media/coverall.6cb90dbc.png) no-repeat 0 9999px;

}

.top-wrap{
    background: url(data:image/png;base64,) no-repeat;
}

.to-singer a{
    background: url(../../static/media/button2.30a1d480.png) no-repeat;
}

.icon-vip{
    background: url(../../static/media/icon.29cd8439.png) no-repeat 0 9999px;
}

.prg-long, .rdy{
