.bannar{
    position: relative;
    width: 982px;
    height: 336px;
    margin: 0 auto;     
}

.contain {
    position: relative;
    width: 730px;
    height: 336px;
    -webkit-transition: all .8s;
    transition: all .8s;
    color: #fff;
    overflow: hidden;
    cursor: pointer;
}
.contain .ul {
     height: 100%;
     list-style: none;
}
.contain .ul .items {
       position: absolute;
       top: 0px;
       width: 100%;
       height: 100%;
}
.ul li.show{
    display: block;
}
.ul li {
    display: none;
}
.ul li img {
    width: 100%;
    height: 100%;
}
.contain .dots {
     position: absolute;
     left: 50%;
     bottom: 20px;
     height: 16px;
     -webkit-transform: translateX(-50%);
             transform: translateX(-50%);
}
.dots li {
    float: left;
    width: 16px;
    height: 16px;
    margin: 0px 5px;
    border-radius: 50%;
    -webkit-transition: all .3s;
    transition: all .3s;
    background-color: antiquewhite;
    list-style: none;
}
.dots li.active {
   background-color: blue;
}
.control > span{
    z-index: 9999;
    color: #000;
}
.control .left {
    position: absolute;
    top: 50%;
    left: 10px;
    padding: 5px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    margin-left: -68px;
    font-size: 30px;
    cursor: pointer;
    -webkit-user-select:none;
       -moz-user-select:none;
        -ms-user-select:none;
            user-select:none;
    
}
.control .left:hover {
    background-color: rgba(196, 196, 196, 0.3);
}
.control .right {
    position: absolute;
    top: 50%;
    right: 10px;
    padding: 5px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    margin-right: -68px;
    font-size: 30px;
    cursor: pointer;
    -webkit-user-select:none;
       -moz-user-select:none;
        -ms-user-select:none;
            user-select:none;
}

.control .right:hover {
    background-color: rgba(196, 196, 196, 0.3);
}
